.login-form {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-column-start: 2;
}

.login-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.login-button {
  border-radius: 5%;
}

.login-button:hover {
  cursor: pointer;
  background-color: #ddf5f9;
}

.email-input-div, .password-input-div {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1em;
}

.login-input {
  height: 2em;
  width: 20em;
}

@media only screen and (max-width: 400px) {
  .email-input-div, .password-input-div {
    display: block;
  }
}
