.header {
  display: flex;
  justify-content: space-between;
  margin: 3em;
  background-color: #cccccc;
}

.tomato-image {
  height: 5em;
  padding-left: 1em;
}

.log-button {
  width: 8em;
  height: 4em;
  margin-right: 1em;
  margin-top: 1em;
  border-radius: 10%;
}

.log-button:hover {
  cursor: pointer;
  background-color: #ddf5f9;
}

@media only screen and (max-width: 470px) {
  .welcome-banner {
    display: none;
  }
}
